import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { ShortFallDetailsModule } from './short-fall-details.module';
import { ShortFallDetailsComponent } from './short-fall-details.component';


const routes: Routes = [
  {
    path: '',
    component: ShortFallDetailsComponent,
    data: {
      title: 'Short Fall Details'
    }
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class ShortFallDetailsRoutingModule { }
